<template>
    <div id="app">
        <Fheader v-bind:title="header_title"></Fheader>
        <div class="doctor-form">
            <div class="piece">病理类型</div>
            <div class="info-card">
                <template v-for="(type, index) in pathologic_data.pathologic_types">
                    <div v-on:click="typeHandler(index)" class="custom-radio" v-bind:class="[ type.selected ? 'custom-radio-filled' : '' ]">{{ type.name }}</div>
                </template>
                <input v-model="type_value" v-on:input="inputHander('type')" class="input-info" placeholder="其他类型肺癌（请填写）">
            </div>
            <div class="piece">疾病分期</div>
            <div class="info-card">
                <div>
                    <template v-for="(stage, index) in pathologic_data.disease_stages">
                        <div v-on:click="stageHandler(index)" class="custom-radio" v-bind:class="[ stage.selected ? 'custom-checkbox-filled' : '' ]">{{ stage.name }}</div>
                    </template>
                </div>
                <div>
                    <template v-for="(stage, index) in pathologic_data.disease_stages">
                        <template v-if="stage.selected">
                            <template v-for="(stage_child, child_index) in stage.childs">
                                <div v-on:click="childStageHandler(index, child_index)" class="custom-radio" v-bind:class="[ stage_child.selected ? 'custom-checkbox-filled' : '' ]">{{ stage_child.name }}</div>
                            </template>
                        </template>
                    </template>
                </div>
            </div>
            <div class="piece">转移病灶（可多选）</div>
            <div class="info-card">
                <template v-for="(lesion, index) in pathologic_data.metastatic_lesion">
                    <div v-on:click="lesionHandler(index)" class="custom-radio" v-bind:class="[ lesion.selected ? 'custom-radio-filled' : '' ]">{{ lesion.name }}</div>
                </template>
                <input v-model="lesion_value" class="input-info" placeholder="其他类型肺癌（请填写）">
            </div>
            <div class="piece">基因突变</div>
            <div class="info-card">
                <div>
                    <template v-for="(gene, index) in pathologic_data.genic_mutations">
                        <div v-on:click="geneHandler(index)" class="custom-radio" v-bind:class="[ gene.selected ? 'custom-radio-filled' : '' ]">{{ gene.name }}</div>
                    </template>
                </div>
                <div>
                    <template v-for="(gene, index) in pathologic_data.genic_mutations">
                        <template v-if="gene.selected">
                            <template v-for="(gene_child, child_index) in gene.childs">
                                <div v-on:click="geneChildHandler(index, child_index)" class="custom-radio" v-bind:class="[ gene_child.selected ? 'custom-checkbox-filled' : '' ]">{{ gene_child.name }}</div>
                            </template>
                        </template>
                    </template>
                </div>
                <input v-model="gene_value" v-on:input="inputHander('gene')" class="input-info" placeholder="其他类型肺癌（请填写）">
            </div>
            <div class="piece">检测方法</div>
            <div class="info-card">
                <template v-for="(detection, index) in pathologic_data.test_method">
                    <div v-on:click="detectionHandler(index)" class="custom-radio" v-bind:class="[ detection.selected ? 'custom-radio-filled' : '' ]">{{ detection.name }}</div>
                </template>
            </div>
        </div>
		<div class="ft-bar-next" v-on:click="nextHandler">下一步</div>
		<transition name="fade">
			<Toast v-bind:msg="toast_text" v-if="toastshow"></Toast>
		</transition>
    </div>
</template>
<script>
import Fheader from '../../components/common/Fheader'
import Toast from '../../components/common/Toast'
import store from '../../store/store.js'
import utils from '../../utils/utils.js'

export default {
    name: 'app',
    data: function(){
    	return {
    		toastshow: false,
    		toast_text: '',
            header_title: '病理信息',
            nextlink: 'http://www.baidu.com',
            type_value: '',
            stage_value: '',
            lesion_value: '',
            gene_value: '',
            detection_value: '',
            pathologic_data: {
                pathologic_types: [
                    {
                        name: '小细胞肺癌',
                        selected: false
                    },
                    {
                        name: '肺鳞癌',
                        selected: false
                    },
                    {
                        name: '肺腺癌',
                        selected: false
                    },
                    {
                        name: '混合癌',
                        selected: false
                    }
                ],
                disease_stages: [
                    {
                        name: 'I',
                        selected: false,
                        childs: [
                            {
                                name: 'Ia',
                                selected: false
                            },
                            {
                                name: 'Ib',
                                selected: false
                            }
                        ]
                    },
                    {
                        name: 'II',
                        selected: false,
                        childs: [
                            {
                                name: 'IIa',
                                selected: false
                            },
                            {
                                name: 'IIb',
                                selected: false
                            }
                        ]
                    },
                    {
                        name: 'III',
                        selected: false,
                        childs: [
                            {
                                name: 'IIIa',
                                selected: false
                            },
                            {
                                name: 'IIIb',
                                selected: false
                            }
                        ]
                    },
                    {
                        name: 'IV',
                        selected: false,
                        childs: [
                            
                        ]
                    }
                ],
                metastatic_lesion: [
                    {
                        name: '脑部',
                        selected: false
                    },
                    {
                        name: '肝肾',
                        selected: false
                    },
                    {
                        name: '淋巴',
                        selected: false
                    },
                    {
                        name: '胸膜',
                        selected: false
                    },
                    {
                        name: '骨转移',
                        selected: false
                    }
                ],
                genic_mutations: [
                    {
                        name: 'EGFR',
                        selected: false,
                        childs: [
                            {
                                name: 'EGFR18',
                                selected: false
                            },
                            {
                                name: 'EGFR19',
                                selected: false
                            },
                            {
                                name: 'EGFR20',
                                selected: false
                            },
                            {
                                name: 'EGFR21',
                                selected: false
                            }
                        ]
                    },
                    {
                        name: 'ALK',
                        selected: false,
                        childs: [
                            
                        ]
                    },
                    {
                        name: 'ROS1',
                        selected: false,
                        childs: [
                            
                        ]
                    },
                    {
                        name: 'HER2',
                        selected: false,
                        childs: [
                            
                        ]
                    },
                    {
                        name: 'BRAF',
                        selected: false,
                        childs: [
                            
                        ]
                    },
                    {
                        name: 'KARS',
                        selected: false,
                        childs: [
                            
                        ]
                    },
                    {
                        name: 'RET',
                        selected: false,
                        childs: [
                            
                        ]
                    },
                    {
                        name: 'ERCC1',
                        selected: false,
                        childs: [
                            
                        ]
                    },
                    {
                        name: 'RRM1',
                        selected: false,
                        childs: [
                            
                        ]
                    },
                    {
                        name: 'cEMT',
                        selected: false,
                        childs: [
                            
                        ]
                    },
                    {
                        name: 'T790M',
                        selected: false,
                        childs: [
                            
                        ]
                    }
                ],
                test_method: [
                    {
                        name: '组织标本（活检）',
                        selected: false
                    },
                    {
                        name: '血液标本',
                        selected: false
                    },
                    {
                        name: '胸水蜡块',
                        selected: false
                    }
                ]
            }
    	}
    },
    mounted: function(){
        var that = this;
        that.initData();
    },
    methods:{
    	showToast: function(){
    		var that = this;
    		if(that.toastshow){
    			return;
    		}
    		that.toastshow = true;
    		setTimeout(function(){
    			that.toastshow = false;
    		}, 1500);
    	},
        initData: function(){
            var that = this;
            store.get_pathologic().then(function(data){
                console.log(data);
                if(data.data.code == 0){
                    that.pathologic_data = data.data.data;
                }else{

                }
            }).catch(function(error){
                console.log(error);
            });
        },
        typeHandler: function(index){
            var that = this;
            that.type_value = '';
            for(var i=0;i<that.pathologic_data.pathologic_types.length;i++){
                that.pathologic_data.pathologic_types[i].selected = false;
            }
            that.pathologic_data.pathologic_types[index].selected = true;
        },
        stageHandler: function(index){
            var that = this;
            for(var i=0;i<that.pathologic_data.disease_stages.length;i++){
                that.pathologic_data.disease_stages[i].selected = false;
            }
            that.pathologic_data.disease_stages[index].selected = true;
        },
        childStageHandler: function(index, child_index){
            var that = this;
            for(var i=0;i<that.pathologic_data.disease_stages[index].childs.length;i++){
                that.pathologic_data.disease_stages[index].childs[i].selected = false;
            }
            that.pathologic_data.disease_stages[index].childs[child_index].selected = true;
        },
        lesionHandler: function(index){
            var that = this;
            that.pathologic_data.metastatic_lesion[index].selected = !that.pathologic_data.metastatic_lesion[index].selected;
        },
        geneHandler: function(index){
            var that = this;
            that.gene_value = '';
            for(var i=0;i<that.pathologic_data.genic_mutations.length;i++){
                that.pathologic_data.genic_mutations[i].selected = false;
            }
            that.pathologic_data.genic_mutations[index].selected = true;
        },
        geneChildHandler: function(index, child_index){
            var that = this;
            for(var i=0;i<that.pathologic_data.genic_mutations[index].childs.length;i++){
                that.pathologic_data.genic_mutations[index].childs[i].selected = false;
            }
            that.pathologic_data.genic_mutations[index].childs[child_index].selected = true;
        },
        detectionHandler: function(index){
            var that = this;
            for(var i=0;i<that.pathologic_data.test_method.length;i++){
                that.pathologic_data.test_method[i].selected = false;
            }
            that.pathologic_data.test_method[index].selected = true;
        },
        inputHander: function(type){
            var that = this;
            switch(type){
                case 'type':
                    if(utils.trim(that.type_value)){
                        for(var i=0;i<that.pathologic_data.pathologic_types.length;i++){
                            that.pathologic_data.pathologic_types[i].selected = false;
                        }
                    }
                break;
                case 'stage':
                    if(utils.trim(that.stage_value)){
                        for(var i=0;i<that.pathologic_data.disease_stages.length;i++){
                            that.pathologic_data.disease_stages[i].selected = false;
                        }
                    }
                break;
                case 'gene':
                    if(utils.trim(that.gene_value)){
                        for(var i=0;i<that.pathologic_data.genic_mutations.length;i++){
                            that.pathologic_data.genic_mutations[i].selected = false;
                            if(that.pathologic_data.genic_mutations[i].childs.length>0){
                                for(var j=0;j<that.pathologic_data.genic_mutations[i].childs.length;j++){
                                    that.pathologic_data.genic_mutations[i].childs[j].selected = false;
                                }
                            }
                        }
                    }
                break;
                case 'detection':
                    if(utils.trim(that.detection_value)){
                        for(var i=0;i<that.pathologic_data.test_method.length;i++){
                            that.pathologic_data.test_method[i].selected = false;
                        }
                    }
                break;
            }
        },
        nextHandler: function(){
            var that = this;
            var data = that.getFormData();
            console.log(data);
            if(!data.pathologic_type){
                that.toast_text = '请选择病理类型！';
                that.showToast();
                return;
            }
            if(!data.disease_stage){
                that.toast_text = '请选择病理分期！';
                that.showToast();
                return;
            }
            if(data.metastatic_lesion.length<=0){
                that.toast_text = '请选择转移病灶！';
                that.showToast();
                return;
            }
            if(!data.genic_mutation){
                that.toast_text = '请选择基因突变！';
                that.showToast();
                return;
            }
            if(!data.test_method){
                that.toast_text = '请选择检测方法！';
                that.showToast();
                return;
            }
            store.pathologic(data).then(function(data){
                console.log(data);
                if(data.data.code == 0){
                    location.href = store.site_links.tomour;
                }else{
                    that.toast_text = data.data.message;
                    that.showToast();
                }
            }).catch(function(error){
                console.log(error);
            });
        },
        getFormData: function(){
            //dealData处理成form数据，data对象key对应后端接口
            var that = this;
            var data = {};
            var type_value = utils.trim(that.type_value);
            var stage_value = utils.trim(that.stage_value);
            var lesion_value = utils.trim(that.lesion_value);
            var gene_value = utils.trim(that.gene_value);
            data.metastatic_lesion = [];
            //获取病理类型
            if(type_value){
                data.pathologic_type = type_value;
            }else{
                for(var i=0;i<that.pathologic_data.pathologic_types.length;i++){
                    if(that.pathologic_data.pathologic_types[i].selected){
                        data.pathologic_type = that.pathologic_data.pathologic_types[i].name;
                    }
                }
            }
            //获取病理分期
            for(var i = 0;i<that.pathologic_data.disease_stages.length;i++){
                if(that.pathologic_data.disease_stages[i].childs.length<=0){
                    if(that.pathologic_data.disease_stages[i].selected){
                        data.disease_stage = that.pathologic_data.disease_stages[i].name;
                    }
                }else{
                    for(var j=0;j<that.pathologic_data.disease_stages[i].childs.length;j++){
                        if(that.pathologic_data.disease_stages[i].childs[j].selected){
                            data.disease_stage = that.pathologic_data.disease_stages[i].childs[j].name;
                        }
                    }
                }
            }
            //获取转移病灶
            if(lesion_value){
                data.metastatic_lesion.push(lesion_value);
            }
            for(var i=0;i<that.pathologic_data.metastatic_lesion.length;i++){
                if(that.pathologic_data.metastatic_lesion[i].selected){
                    data.metastatic_lesion.push(that.pathologic_data.metastatic_lesion[i].name);
                }
            }
            //获取基因
            if(gene_value){
                data.genic_mutation = that.gene_value;
            }else{
                for(var i = 0;i<that.pathologic_data.genic_mutations.length;i++){
                    if(that.pathologic_data.genic_mutations[i].childs.length<=0){
                        if(that.pathologic_data.genic_mutations[i].selected){
                            data.genic_mutation = that.pathologic_data.genic_mutations[i].name;
                        }
                    }else{
                        for(var j=0;j<that.pathologic_data.genic_mutations[i].childs.length;j++){
                            if(that.pathologic_data.genic_mutations[i].childs[j].selected){
                                data.genic_mutation = that.pathologic_data.genic_mutations[i].childs[j].name;
                            }
                        }
                    }
                }
            }
            //获取检测方法
            for(var i=0;i<that.pathologic_data.test_method.length;i++){
                if(that.pathologic_data.test_method[i].selected){
                    data.test_method = that.pathologic_data.test_method[i].name;
                }
            }
            return data;
        }
    },
    components: {
    	Fheader,
        Toast
    }
}
</script>

<style>
@import '../../assets/css/common.css';
@import '../../assets/css/date-picker-bottom.css';
.doctor-form{
    padding: 10px 15px 60px 15px;
}
</style>



